import React from 'react';
import '../../../src/components/intro/intro.css';
import bg from '../../../src/assets/image.png';
import btnImg from '../../../src/assets/hireme.png';
import {Link} from 'react-scroll';

const Intro = () => {
  return (
    <section id="intro">
        <div className='introContent'>
            <span className='hello'>Hello, </span>
            <span className='introText'>I'm <span className='introName'>Tensaey</span> <br/>a Website Developer</span>
            <p className='introPara'>I am a skilled web developer with experience <br/>in creating visually appealing and user friendly websites.</p>
            <Link activeClass='active' to='contact' smooth={true} offset={-100} duration={500}><button className='btn'><img src={btnImg} alt='Hire Me' className='btnImg'/> Hire Me</button></Link>
        </div>
        <img src={bg} alt='Profile' className='bg'/>
    </section>
  )
}

export default Intro;