import React from 'react';
import '../../../src/components/skills/skills.css';
import UIDesign from '../../../src/assets/ui-design.png';
import WebDesign from '../../../src/assets/website-design.png';

const Skills = () => {
    return (
        <section id='skills'>
            <span className='skillTitle'>What I do</span>
            <span className='skillDesc'>I am a skilled and passionte web developer with experience in creating visually appealing and user-friendly websites. I have a strong understanding of design and a keen eye for detail. I am proficient in HTML, CSS, and JavaScript, as well as design software such as Adobe Photoshop and Illustrator.</span>
            <div className='skillBars'>
                <div className='skillBar'>
                    <img src={UIDesign} alt='UIDesing' className='skillBarImg'/>
                    <div className='skillBarText'>
                        <h2>UI/UX Design</h2>
                        <p>I craft engaging and intuitive user experiences through thoughtful UI/UX design.</p>
                    </div>
                </div>
                <div className='skillBar'>
                    <img src={WebDesign} alt='WebDesign' className='skillBarImg'/>
                    <div className='skillBarText'>
                        <h2>Website Developing</h2>
                        <p>I bring web ideas to life through expert coding and development.</p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skills